import React, { useEffect } from "react";
import {  DatePicker, Form, Input, Modal } from "antd";
import moment from "moment";
import { useEpicsModal, useEpicsQueryKey } from "./util";
import { useEditEpic } from "utils/epic";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};

export const EpicModal = () => {
  const [form] = Form.useForm();
  const { editingEpicId, editingEpic, close } =  useEpicsModal();
  const { mutateAsync: editEpic, isLoading: editLoading } = useEditEpic(
    useEpicsQueryKey()
  );

  const onCancel = () => {
    close();
    form.resetFields();
  };

  const onOk = async () => {
    await editEpic({ ...editingEpic, ...form.getFieldsValue() });
    close();
  };

  
  useEffect(() => {
    form.setFieldsValue( {...editingEpic,
      start : moment(editingEpic?.start),
      end : moment(editingEpic?.end)
    });
  }, [form, editingEpic]);

  return (
    <Modal
      forceRender={true}
      onCancel={onCancel}
      onOk={onOk}
      okText={"确认"}
      cancelText={"取消"}
      confirmLoading={editLoading}
      title={"编辑任务"}
      visible={!!editingEpicId}
    >
      <Form {...layout} 
      form={form}>
        <Form.Item
          label={"任务组名"}
          name={"name"}
          rules={[{ required: true, message: "请输入任务组名" }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label={"开始时间"}
          name={"start"}
          rules={[{ required: true, message: "请输入开始时间" }]}
        >
            <DatePicker   
                  placeholder={"请输入任务组开始时间"}  
                  style={{width: "100%"}}
                  format="YYYY-MM-DD"
                  />
        </Form.Item>
        <Form.Item
          label={"结束时间"}
          name={"end"}
          rules={[{ required: true, message: "请输入结束时间" }]}
        >
           <DatePicker   
                  placeholder={"请输入任务组结束时间"}  
                   style={{width: "100%"}}/>
        </Form.Item>
     
      </Form>
     
    </Modal>
  );
};
