import { useCallback } from "react";
import { useProjectIdInUrl } from "screens/kanban/util";
import { useEpic } from "utils/epic";
import { useUrlQueryParam } from "utils/url";

export const useEpicSearchParams = () => ({ projectId: useProjectIdInUrl() });

export const useEpicsQueryKey = () => ["epics", useEpicSearchParams()];

export const useEpicsModal = () => {
  const [{ editingEpicId }, setEditingEpicId] = useUrlQueryParam([
    "editingEpicId",
  ]);
  const { data: editingEpic, isLoading } = useEpic(Number(editingEpicId));
  const startEdit = useCallback(
    (id: number) => {
    
      setEditingEpicId({ editingEpicId: id });
    },
    [setEditingEpicId]
  );
  const close = useCallback(() => {
    setEditingEpicId({ editingEpicId: "" });
  }, [setEditingEpicId]);
  return {
    editingEpicId,
    editingEpic,
    startEdit,
    close,
    isLoading,
  };
};
