import React, { useState } from "react";
import { Row, ScreenContainer } from "components/lib";
import { useProjectInUrl } from "screens/kanban/util";
import { useDeleteEpic, useEpics } from "utils/epic";
import { Button, List, Modal } from "antd";
import { useTasks } from "utils/task";
import { Link } from "react-router-dom";
import { useEpicSearchParams, useEpicsModal, useEpicsQueryKey } from "screens/epic/util";
import { Epic } from "types/epic";
import { CreateEpic } from "screens/epic/create-epic";
import { EpicModal } from "./epic-modal";
import moment from "moment";
export const EpicScreen = () => {
  const { data: currentProject } = useProjectInUrl();
  const { data: epics } = useEpics(useEpicSearchParams());
  const { data: tasks } = useTasks({ projectId: currentProject?.id });
  const { mutate: deleteEpic } = useDeleteEpic(useEpicsQueryKey());
  const [epicCreateOpen, setEpicCreateOpen] = useState(false);
  const { startEdit } = useEpicsModal();
  const confirmDeleteEpic = (epic: Epic) => {
    Modal.confirm({
      title: `确定删除项目组：${epic.name}`,
      content: "点击确定删除",
      okText: "确定",
      cancelText : "取消",
      onOk() {
        deleteEpic({ id: epic.id });
      },
    });
  };
  
  console.log(moment("1995-12-25"));
  
  return (
    <ScreenContainer>
      <Row between={true}>
        <h1>{currentProject?.name}任务组</h1>
        <Button onClick={() => setEpicCreateOpen(true)} type={"link"}>
          创建任务组
        </Button>
      </Row>
      <List
        style={{ overflow: "scroll" }}
        dataSource={epics}
        itemLayout={"vertical"}
        renderItem={(epic) => (
          <List.Item >
            <List.Item.Meta
              title={
                <Row between={true}>
                  <span>{epic.name}</span>
                  <div>
                    <Button onClick={() => startEdit(epic.id)} type={"link"}>
                      编辑 
                    </Button>
                    <Button onClick={() => confirmDeleteEpic(epic)} type={"link"}>
                      删除
                    </Button>
                  </div>
                
                </Row>
              }
              description={
                <div>
                  <div>开始时间：{moment(epic.start).format("YYYY-MM-DD")}</div>
                  <div>结束时间：{moment(epic.end).format("YYYY-MM-DD")}</div>
                </div>}
              
            />
            <div>
              {tasks
                ?.filter((task) => task.epicId === epic.id)
                .map((task) => (<div key={task.id} style={{display:"inline-block"}}>
                  <Link
                    to={`/projects/${currentProject?.id}/kanban?editingTaskId=${task.id}`}
                  >
                    {task.name}
                  </Link>
                  <span style={{margin : "0 1rem"}}> | </span>
                </div>
                ))}
            </div>
          </List.Item>
        )}
      />
      <CreateEpic
        onClose={() => setEpicCreateOpen(false)}
        visible={epicCreateOpen}
      />
      <EpicModal/>
    </ScreenContainer>
  );
};
